export type Direction = 'top' | 'right' | 'bottom' | 'left';
export const Direction = null;

const attachments = <{ [key: string]: any }>{
  top: { attachment: 'bottom center', offset: '10px 0', opposite: 'top center' },
  left: { attachment: 'middle right', offset: '0 10px', opposite: 'middle left' },
  right: { attachment: 'middle left', offset: '0 -10px', opposite: 'middle right' },
  bottom: { attachment: 'top center', offset: '-10px 0', opposite: 'bottom center' },
  lb: { attachment: 'top left', offset: '-10px 0', opposite: 'bottom left' },
  rb: { attachment: 'top right', offset: '-10px 0', opposite: 'bottom right' },
  rt: { attachment: 'bottom right', offset: '10px 0', opposite: 'top right' },
  lt: { attachment: 'bottom left', offset: '10px 0', opposite: 'top left' },

};

const PLACEMENTS = Object.keys(attachments).reduce((placements: any, direction: Direction) => {
  const { attachment, offset, opposite } = attachments[direction];
  const targetAttachment = opposite;

  placements[direction] = { opposite, attachment, targetAttachment, offset };
  return placements;
}, {});

export function placement(direction: Direction) {
  return PLACEMENTS[direction];
}
